<template>
  <div id="slbase-set">
    <h3 class="slbase-title clearfix">
        <span class="left">基础设置</span>
        <router-link :to='{path:"/shopinfo/shopseting"}'>
            <span class="right editor">编辑</span>
        </router-link>
    </h3>

    <section class="base-content">

        <div class="base-item">
            <div class="title">营业时间：</div>
            <div class="content">
                <span>{{opentime_start}}</span>
                -
                <span>{{opentime_end}}</span>
            </div>
        </div>

        <div class="base-item paytime">
            <div class="title">餐时：</div>
            <div class="content">
                <span class="paytime-item" v-for="item in openingtime_arr">
                    {{item.title}} : {{item.start}}-{{item.end}}
                </span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">启用支付方式：</div>
            <div class="content">
                <span>{{ payway_str }}</span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">付款时机：</div>
            <div class="content">
                <span>{{ paytime_str }}</span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">是否提供发票：</div>
            <div class="content">
                <span>{{ invoce_str }}</span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">发票备注：</div>
            <div class="content">
                <span>{{ invoc_remark_str }}</span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">扫码点餐系统：</div>
            <div class="content">
                <span>{{ onoff_str }}</span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">销售方式：</div>
            <div class="content">
                <span>{{ saleway_str }}</span>
            </div>
        </div>

        <div class="base-item">
            <div class="title">店铺标签：</div>
            <div class="content">
                <span>{{ shoplabel_str }}</span>
            </div>
        </div>

        <div class="base-item shop-img">
            <div class="title">店铺图片：</div>
            <div class="content clearfix">
                <div v-if="shopimg_list.length === 0" class="img-content">
                    <img src="./img/shop_icon.png">
                </div>
                <div v-else class="img-content left" v-for="item in shopimg_list">
                    <img :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + item">
                </div>
            </div>
        </div>


    </section>
  </div>
</template>

<script>
import { Util } from "@/config/util";
import { getShopinfo } from "../api";
import {
  Payway,
  PayTime,
  Invoce,
  Startsystem,
  Saleway,
  Dinnertime
} from "@/config/cfg";

export default {
  data() {
    return {
      opentime_start: "00:00:00",
      opentime_end: "23:59:59",
      payway_str: "现金支付、刷卡支付、支付宝支付、微信支付、挂账",
      paytime_str: "餐前支付、餐后支付",
      invoce_str: "不提供发票",
      invoc_remark_str: "- -",
      onoff_str: "启用",
      saleway_str: "在店吃、外卖、打包、自提",
      shoplabel_str: "- -",
      shopimg_list: [],
      openingtime_arr: [
        {
          title: "早市",
          start: "06:00:00",
          end: "09:59:59"
        },
        {
          title: "午市",
          start: "10:00:00",
          end: "15:59:59"
        },
        {
          title: "晚市",
          start: "16:00:00",
          end: "21:59:59"
        },
        {
          title: "夜宵",
          start: "22:00:00",
          end: "5:59:59"
        }
      ],

      imgbase_url: imgbase_url
    };
  },
  created() {
    this.getdata();
  },
  methods: {
    //获取数据
    getdata() {
      getShopinfo(
        {
          get_shopinfo_edit: 1
        },
        resp => {
          if (resp.ret === 0) {
            let shopinfo = resp.data.shopinfo || {};

            // 营业时间
            if (!Util.isEmpty(shopinfo.opentime_start)) {
              this.opentime_start = (shopinfo.open_time || [])[0];
              this.opentime_end = (shopinfo.open_time || [])[1];
            }

            // 启用支付方式

            if (!Util.isEmpty(shopinfo.shop_pay_way)) {
              let paywayArr = shopinfo.shop_pay_way || [];
              paywayArr = paywayArr.map(item => {
                return Payway.toString(item);
              });
              this.payway_str = paywayArr.join("、");
            }

            //付款时机
            if (!Util.isEmpty(shopinfo.pay_time)) {
              let paytimeArr = shopinfo.pay_time || [];
              paytimeArr = paytimeArr.map(item => {
                return PayTime.toString(item);
              });
              this.paytime_str = paytimeArr.join("、");
            }

            //是否提供发票
            if (!Util.isEmpty((shopinfo.is_invoice_vat || {}).is_invoice)) {
              this.invoce_str = Invoce.toString(
                (shopinfo.is_invoice_vat || {}).is_invoice
              );
            }
            //发票备注
            if (!Util.isEmpty(shopinfo.invoice_remark)) {
              this.invoc_remark_str = shopinfo.invoice_remark;
            }
            //是否启用扫码点餐系统
            if (!Util.isEmpty(shopinfo.suspend)) {
              this.onoff_str = Startsystem.toString(shopinfo.suspend);
            }
            //销售方式
            if (!Util.isEmpty(shopinfo.sale_way)) {
              let salewayArr = shopinfo.sale_way || [];
              salewayArr = salewayArr.map(item => {
                return Saleway.toString(item);
              });
              this.saleway_str = salewayArr.join("、");
            }
            //店铺标签
            if (!Util.isEmpty(shopinfo.shop_label)) {
              this.shoplabel_str = (shopinfo.shop_label || []).join("、");
            }
            //店铺图片
            this.shopimg_list = shopinfo.img_list || [];
            //餐时
            if (
              !Util.isEmpty(shopinfo.opening_time) &&
              shopinfo.opening_time.length !== 0
            ) {
              let openingtimeArr = shopinfo.opening_time || [];
              openingtimeArr = openingtimeArr.map(item => {
                let from = item.from || {};
                let to = item.to || {};

                item["title"] = Dinnertime.toString(item.type);
                item["start"] = from.hh + ":" + from.mm + ":" + from.ss;
                item["end"] = to.hh + ":" + to.mm + ":" + to.ss;

                return item;
              });

              this.openingtime_arr = openingtimeArr;
            }
          }
        }
      );
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
#slbase-set {
  @include fc(14px, #000);
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
}
.slbase-title {
  @include fc(14px, #666666);
  background-color: #f6f8fc;
  height: 40px;
  line-height: 40px;
  padding: 0 14px;

  .editor {
    color: #5a8cff;
    cursor: pointer;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      left: -22px;
      top: 9px;
      @include wh(20px, 20px);
      background: url("./img/edit.png") no-repeat;
    }
  }
}
.base-content {
  @include wh(100%, 715px);
  padding: 40px 14px 0;
}
.base-item {
  margin-bottom: 20px;
  div {
    display: inline-block;
  }
  .title {
    color: #666666;
  }
}
.base-item.paytime {
  div.content {
    .paytime-item {
      padding: 0 40px 0 20px;
    }
  }
}
.base-item.shop-img {
  div {
    display: block;
  }
  div.title {
    margin-bottom: 10px;
  }
  div.content {
    .img-content {
      @include wh(100px, 100px);
      margin-right: 30px;
      border-radius: 3px;
      img {
        @include wh(100px, 100px);
        border-radius: 3px;
      }
    }
  }
}
</style>





